 


const ExercicioList = ({ treino }) => {
    const exerciciosPorTreino = {
      Treino_Abdomen: [
        { nome: 'Abdominal canivete', videoUrl: '/Treino_Abdomen/Abdominal_canivete.mp4' },
        { nome: 'Prancha', videoUrl: '..public/Treino_Abdomen/Abdominal canivete.mp4' },
      ],
      Treino_Bicpes: [
        { nome: 'Rosca Direta', videoUrl: '/Treino_Abdomen/Abdominal_canivete.mp4' },
        { nome: 'Rosca Alternada', videoUrl: '..public/Treino_Abdomen/Abdominal canivete.mp4' },
      ],
      Treino_Costas: [
        { nome: 'Puxada na Barra', videoUrl: '/videos/costas1.mp4' },
        { nome: 'Remada Curvada', videoUrl: '/videos/costas2.mp4' },
      ],
      Treino_Ombro: [
        { nome: 'Desenvolvimento', videoUrl: '/videos/ombro1.mp4' },
        { nome: 'Elevação Lateral', videoUrl: '/videos/ombro2.mp4' },
      ],
      Treino_Peito: [
        { nome: 'Supino Reto', videoUrl: '/videos/peito1.mp4' },
        { nome: 'Crucifixo', videoUrl: '/videos/peito2.mp4' },
      ],
      Treino_Pernas: [
        { nome: 'Agachamento', videoUrl: '/videos/pernas1.mp4' },
        { nome: 'Leg Press', videoUrl: '/videos/pernas2.mp4' },
      ],
      Treino_Triceps: [
        { nome: 'Tríceps Pulley', videoUrl: '/videos/triceps1.mp4' },
        { nome: 'Mergulho', videoUrl: '/videos/triceps2.mp4' },
      ],
    };
  
    const exercicios = exerciciosPorTreino[treino] || [];
  
    return (
      <div className="flex-1 p-6">
        <h2 className="text-2xl font-semibold mb-4">{treino}</h2>
        <ul>
          {exercicios.map((exercicio, index) => (
            <li key={index} className="mb-4">
              <div className="text-lg font-medium">{exercicio.nome}</div>
              <video controls className="mt-2 w-full max-w-md">
                <source src={exercicio.videoUrl} type="video/mp4" />
                Seu navegador não suporta o formato de vídeo.
              </video>
            </li>
          ))}
        </ul>
      </div>
    );
  };
  
  export default ExercicioList;
  