import { useState, useEffect } from 'react';

const Sidebar = ({ setMenuItem }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false); // Estado para controlar o tamanho da tela

  // Efeito para verificar o tamanho da janela após o carregamento
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 1024); // Define como "mobile" se a largura for menor que 1024px
    };

    handleResize(); // Chama a função imediatamente para definir o estado inicial
    window.addEventListener('resize', handleResize); // Adiciona ouvinte de resize

    // Limpa o ouvinte quando o componente é desmontado
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const menuItems = [
    'Treino_Abdomen',
    'Treino_Bicpes',
    'Treino_Costas',
    'Treino_Ombro',
    'Treino_Peito',
    'Treino_Pernas',
    'Treino_Triceps',
  ];

  return (
    <div className="lg:flex lg:flex-col bg-gray-800 text-white p-4 w-64 h-screen">
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="lg:hidden text-white mb-4"
      >
        {isOpen ? 'Fechar Menu' : 'Abrir Menu'}
      </button>
      {(isOpen || !isMobile) && ( // Exibe o menu se for maior que 1024px ou o menu estiver aberto no mobile
        <ul>
          {menuItems.map((item) => (
            <li
              key={item}
              className="cursor-pointer py-2 px-4 hover:bg-gray-700"
              onClick={() => setMenuItem(item)}
            >
              {item}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Sidebar;
